.main-container .fulu-cell-box.line-box {
    padding: 0;
}
.main-container .fulu-cell-box.line-box .ant-divider-horizontal {
    margin: 10px 0;
}
.line-box:hover .delete-line {
    display: initial;
}
.line-box .delete-line {
    display: none;
    position: absolute;
    font-size: 16px;
    right: 20px;
    top: 50%;
    margin-top: -13px;
    cursor: pointer;
}