.main-container {
	position: relative;
	margin: 10px 8px 10px 5px;
	background-color: #f0f2f5;
	min-height: 400px;
	outline-color:#1890ff;
	overflow: hidden;
}
.main-container .btn-manage {
    position: absolute;
    padding: 0;
    width: 40px;
    right: 8px;
    bottom: 8px;
    z-index: 99;
}
.panel-header .edit-modal {
    cursor: pointer;
    color: #1890ff;
    margin-left: 10px;
}
.panel-header .edit-modal:hover {
    text-decoration: underline;
}
.main-container .fulu-cell-box {
    position: relative;
    background: #fff;
	font-size: 0;
    padding: 8px;
}
.main-container .fulu-cell-box:after {
    content: '';
    display: table;
    clear: both;
}
.main-container .fulu-cell-box .cell {
    float: left;
    min-height: inherit;
    font-size: 14px;
}
.edit-mode {
    margin: 24px 14px 10px;
}
.edit-mode .edit-mode {
    margin: 10px 0 24px;
}

.edit-mode .drop-container>div {
    text-align: left;
}
.ant-form:after {
	content: '';
	display: table;
	clear: both;
}