body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root {
    height: inherit;
}
.App {
    text-align: center;
    height: inherit;
    display: -webkit-flex;
    display: flex;
}
.ant-layout-sider {
    background-color: #fff !important;
}
.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}
  
.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
  
.App-link {
    color: #61dafb;
}
  
.container {
    position: relative;
}
  
.container .icon-operation, .fulu-cell-box>.icon-operation, .drop-container>.icon-operation {
    position: absolute;
    right: 0;
    top: 0;
    display: none;
    font-size: 14px;
}
.container .icon-operation, .fulu-cell-box>.icon-operation {
    right: -4px;
    top: -4px;
}
.container .icon-operation.anticon-close-circle {
    top: -6px;
    right: -6px;
}
.container:hover .icon-operation, .fulu-cell-box:hover .icon-operation {
    display: block;
}


.preview-box .ant-modal, .preview-box .ant-modal .ant-modal-content {
    height: 100%;
}
.preview-box .ant-modal {
    padding-bottom: 0;
}
.fulu-box {
    padding: 24px;
    background: #fff;
}
.table-box {
    margin-bottom: 16px;
    -webkit-flex: 1 1;
            flex: 1 1;
}
.table-box .ant-table-thead td {
    background: #d6d6d6;
}
.btn-group button:not(:last-child) {
    margin-right: 10px;
}
.anticon-drag {
    margin: auto;
    font-size: 18px;
    color: #d6d2d2;
}

.main-container.preview-mode {
    margin: 0;
    outline: none;
    min-height: calc(100vh - 55px);
}

.fulu-btn-collapse {
    cursor: pointer;
}

.fulu-btn-collapse .anticon{
    margin-left: 5px;
}

.fulu-btn-collapse:hover {
    color: #096dd9;
}

.wrapper {
    padding: 24px;
    background: #f0f2f5;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: inherit;
}

.position-fixed {
    position: absolute !important;
    width: 100%;
    padding: 0 24px;
}
.position-fixed+div {
    margin-top: 90px !important;
}
.position-fixed+div.preview-mode {
    margin-top: 74px !important;
}
.clear-box:after {
    content: '';
    display: table;
    clear: both;
}
@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}  
.form-box {
    margin-bottom: 24px;
}
.form-box .btn-collapse {
    margin-left: 8px;
    font-size: 12px;
    text-decoration: none;
}
.ant-form-item.half-item {
    display: inline-block !important;
    width: 45%;
}
.ant-form-item {
    margin-bottom: 24px;
    margin-right: 0 !important;
    display: -webkit-flex !important;
    display: flex !important;
}
.form-container .anticon-drag {
    line-height: 42px;
}
.ant-form-item-control-wrapper {
    -webkit-flex: 1 1;
            flex: 1 1;
}
.preview-mode .border-box {
    outline: none;
}
.border-box {
    outline: 1px dashed #d9d9d9;
}
.border-box:hover {
    outline-color: #1890ff;
}
.preview-box.border-box {
    outline: none;
}
.line-box {
    margin: 0 24px;
}
/********drop-container*********/
.drop-container {
    position: relative;
    display: -webkit-flex;
    display: flex;
    vertical-align: bottom;
    width: 100%;
    min-height: 50px;
    border: none;
    -webkit-align-items: center;
            align-items: center;
}
.drop-container>div {
    width: 100%;
}
.drop-container .ant-tabs-bar {
    margin-bottom: 0;
}
.drop-container:hover>.icon-operation {
    display: block;
}
.drop-container>.icon-operation {
    right: 0;
    top: 0;
    display: none;
}


.edit-name-box {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    background: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.edit-name-box .ant-form {
    padding: 40px !important;
    border: 1px solid #cecccc;
    border-radius: 2px;
}
.main-container .fulu-cell-box.line-box {
    padding: 0;
}
.main-container .fulu-cell-box.line-box .ant-divider-horizontal {
    margin: 10px 0;
}
.line-box:hover .delete-line {
    display: inline;
    display: initial;
}
.line-box .delete-line {
    display: none;
    position: absolute;
    font-size: 16px;
    right: 20px;
    top: 50%;
    margin-top: -13px;
