}
.btn-add-box {
    margin: 10px 0 25px;
}

.ant-row.ant-form-item {
    margin-bottom: 0;
}
.panel-header .btn-delete {
    float: right;
    margin: 5px;
}
.ant-table {
    cursor: pointer;
}
.table-container {
    background: #fff;
    margin-bottom: 16px;
    margin: 10px;
}
.table-container table .ant-table-thead th {
    padding-top: 9px;
    padding-bottom: 9px; 
}

.btn-bottom-box {
    position: absolute;
    right: 10px;
    bottom: 0;
    padding: 10px 16px;
    text-align: right;
}

.tips-box {
    margin: 15px 0;
    font-size: 80%;
    padding: 0 5px;
    color: #fb7c6d;
}
.tips-box .anticon {
    margin-right: 5px;
}
.btn-add-box {
    text-align: center;
    margin-top: 10px;
    margin-bottom: 0;
    padding: 5px 0;
}
.main-container .header-box {
    padding: 0;
    margin: 0;
    min-height: 50px;
}
.main-container .header-box .ant-breadcrumb {
    padding-left: 24px;
}
.main-container .header-box .drop-container>div {
    text-align: left;
}
.btn-box {
    text-align: center;
    margin: 10px 0;
}
.btn-bottom-box {
    position: absolute;
    right: 10px;
    bottom: 0;
    padding: 10px 16px;
    text-align: right;
}
.form-container .drop-container {
    position: relative;
    display: inline-block;
    min-height: 42px;
    text-align: center;
}
.form-container {
    position: relative;
    font-size: 0;
    margin: 10px;
}
.form-container .inner-box {
    min-height: 102px;
