}
.layout-container {
    cursor: pointer;
    min-height: 100px;
    border: 1px dashed #ccc;
    margin-bottom: 10px;
    padding: 5px 24px;
    font-size: 0;
}
.layout-container .ant-form {
    text-align: left;
}
.empty-tips {
    position: absolute;
    left: 50%;
    top: 50%;
    border: 2px dashed #ccc;
    line-height: 50px;
    padding: 0 5px;
    margin-left: -56px;
    margin-top: -27px;
}
.aside-tabs .ant-tabs-tabpane {
    text-align: left;
    padding: 0 10px;
}
.aside-tabs .ant-tabs-tabpane .drag-item {
    display: inline-block;
    border: 1px solid #cccccc;
    padding: 5px 10px;
    cursor: move;
    margin: 5px;
}
.aside-tabs .ant-tabs-tabpane .drag-item:hover {
    border-color: #1890ff;
    color: #1890ff;
}



