}
.login-wrapper {
    position: absolute;
    -webkit-transition: all ease-in-out 0.3s;
    transition: all ease-in-out 0.3s;
    height: 300px;
    width: 928px;
}
.login-wrapper.slide-l {
    -webkit-transform: translateX(-476px);
            transform: translateX(-476px);
}
.login-wrapper .url-container, .login-wrapper .iframe-container {
    position: relative;
    height: inherit;
