.aside-tabs .ant-tabs-tabpane {
    text-align: left;
    padding: 0 10px;
}
.aside-tabs .ant-tabs-tabpane .drag-item {
    display: inline-block;
    border: 1px solid #cccccc;
    padding: 5px 10px;
    cursor: move;
    margin: 5px;
}
.aside-tabs .ant-tabs-tabpane .drag-item:hover {
    border-color: #1890ff;
    color: #1890ff;
}