.wrapper {
    padding: 24px;
    background: #f0f2f5;
    display: flex;
    flex-direction: column;
    min-height: inherit;
}

.position-fixed {
    position: absolute !important;
    width: 100%;
    padding: 0 24px;
}
.position-fixed+div {
    margin-top: 90px !important;
}
.position-fixed+div.preview-mode {
    margin-top: 74px !important;
}
.clear-box:after {
    content: '';
    display: table;
    clear: both;
}