body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root {
    height: inherit;
}
.App {
    text-align: center;
    height: inherit;
    display: flex;
}
.ant-layout-sider {
    background-color: #fff !important;
}
.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}
  
.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
  
.App-link {
    color: #61dafb;
}
  
.container {
    position: relative;
}
  
.container .icon-operation, .fulu-cell-box>.icon-operation, .drop-container>.icon-operation {
    position: absolute;
    right: 0;
    top: 0;
    display: none;
    font-size: 14px;
}
.container .icon-operation, .fulu-cell-box>.icon-operation {
    right: -4px;
    top: -4px;
}
.container .icon-operation.anticon-close-circle {
    top: -6px;
    right: -6px;
}
.container:hover .icon-operation, .fulu-cell-box:hover .icon-operation {
    display: block;
}


.preview-box .ant-modal, .preview-box .ant-modal .ant-modal-content {
    height: 100%;
}
.preview-box .ant-modal {
    padding-bottom: 0;
}
.fulu-box {
    padding: 24px;
    background: #fff;
}
.table-box {
    margin-bottom: 16px;
    flex: 1 1;
}
.table-box .ant-table-thead td {
    background: #d6d6d6;
}
.btn-group button:not(:last-child) {
    margin-right: 10px;
}
.anticon-drag {
    margin: auto;
    font-size: 18px;
    color: #d6d2d2;
}

.main-container.preview-mode {
    margin: 0;
    outline: none;
    min-height: calc(100vh - 55px);
}

.fulu-btn-collapse {
    cursor: pointer;
}

.fulu-btn-collapse .anticon{
    margin-left: 5px;
}

.fulu-btn-collapse:hover {
    color: #096dd9;
}
